@import "../../../style/theme/default/var.css";
.formula {
  width: 1000px;
  margin: auto;
  padding-top: var(--theme-padding-base);
}

.formula .save-part {
  float: right;
  height: 57px;
  padding: 7px 15px;
  position: relative;
  z-index: 99;
  font-size: 14px;
}

.formula > div {
  position: relative;
  /*    width: 935px; 中间那个会不够长 会换行*/
  height: 57px;
  font-size: 30px;
  border: 1px solid var(--theme-border-color-primary);
  padding: 5px 10px 5px 70px;
  > sub {
  font-size: 20px;
    }
}

.formula > div > input {
  border: none;
  border-bottom: 2px solid var(--theme-border-color-primary);
  /*padding: 5px 10px 5px 70px;*/
  width: 55px;
  text-align: center;
  height: 44px
}

.formula .addon {
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  float: left;
  height: 55px;
  background-color: var(--theme-background-color-primary);
  border-right: 1px solid var(--theme-border-color-primary);
  text-align: center;
  line-height: 1;
  & > i {
    vertical-align: middle;
    font-size: 32px;
    margin-top: 12px;
    display: inline-block;
  }
}

.formula-word {
  line-height: 150%;
  margin-top: 20px;
  > p {
  margin:0 0 var(--theme-margin-small) 0;
      font-size:var(--theme-font-size-small);
    }
}



